body {
  background-color: #8baffc;
  font-family: Arial, sans-serif;
}

.search-form {
  justify-content: center;
  margin: 50px 350px;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  flex: 1;
  padding: 24px;
  font-size: 24px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #007bff;
  border-radius: 5px;
  margin-left: 10px;
  padding: 24px;
  font-size: 24px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding-bottom: 50px;
  display: flex;
}

.photo-card {
  width: 700px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 15px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 15px;
  display: block;
}

.info {
  color: #007bff;
  margin-top: 10px;
  font-size: 24px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #007bff;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 24px;
  font-size: 24px;
}

